export function numberConverter(number) {
    let numRound = null;
    let numString = '';

    //    0  10  
    //   
    if (number.toString().length === 1) {
        numString = String(number) + ".0";
        return numString;
    }

    //    0  10  
    //    
    if (number / 10 < 1) {
        numRound = (Math.round(number * 10) / 10).toString();
        numString = String(numRound).padEnd(3, '0');
        return numString;
    }

    if (number >= 10) {
        return '10.0';
    }

    // console.log(numString);
}

